import { KEYUTIL, KJUR } from 'jsrsasign'

export default class OfflineAuth {
  constructor(storageItemName) {
    this.storageItemName = storageItemName
  }

  auth = () => {
    let isValid = false
    try {
      const token = localStorage.getItem(this.storageItemName)
      if (token) {
        const key = KEYUTIL.getKey(process.env.REACT_APP_RS256_PUBLIC_KEY)
        isValid = KJUR.jws.JWS.verifyJWT(token, key, { alg: ['RS256'] })
      }
      return isValid
    } catch(e) {
      return isValid
    }
  }
}
