import React, { useContext, useEffect, useMemo } from 'react'
import { useHistory } from 'react-router-dom'
import { observer } from 'mobx-react-lite'
import { Emitter, SoundEffectPlayer } from 'shared/services'
import { ClassicDartGameView, GameMessagesView } from 'components'
import { BackgroundImageContext } from 'components/contexts'
import NinjaCountUpGame from './NinjaCountUpGame'

export default function NinjaCountUpGameView(props) {
  const { players, rounds } = props
  const history = useHistory()
  const { backgroundImage, setBackgroundImage } = useContext(BackgroundImageContext)

  players.length === 0 && history.push('/game-menu')

  const game = useMemo(() => new NinjaCountUpGame(players, { rounds }), [players, rounds])
  useEffect(() => {
    if (game.isInitialized()) {
      const originalBackgrondImage = {...backgroundImage}
      const { theme: originalSoundEffectTheme } = SoundEffectPlayer
      setBackgroundImage({ name: 'ninja', url: '/resources/images/background/ninja.png' })
      SoundEffectPlayer.setTheme('ninja')
      SoundEffectPlayer.play('bgm')
      game.start()
      game.onGameOver = () => {
        Emitter.once('CHANGE_BUTTON_CLICK', () => {
          game.clearGameMessages()
          game.addGameMessage('back')
          game.stop()
          setBackgroundImage(originalBackgrondImage)
          SoundEffectPlayer.stop('bgm')
          SoundEffectPlayer.setTheme(originalSoundEffectTheme)
          history.push('/game-menu')
        })
      }
    }
  }, [game, history, backgroundImage, setBackgroundImage])

  const GameView = observer(({ game }) => {
    const {
      currentPlayer: player,
      currentPlayerGameScore: gameScore,
      currentPlayerDartScores: dartScores,
      currentPlayerScoreboard: scoreboard,
      players,
      gameScores
    } = game.state
    return <ClassicDartGameView name='Ninja count-up'
                                player={player}
                                gameScore={gameScore}
                                dartScores={dartScores}
                                scoreboard={scoreboard}
                                players={players}
                                gameScores={gameScores}
                                enableRandomBackgroundImage={false} />
  })
  const MessagesView = observer(({ game }) => {
    const { messages } = game.state
    return <GameMessagesView messages={messages} onClose={game.handleGameMessageOnClose} />
  })
  return (
    <React.Fragment>
      <GameView game={game} />
      <MessagesView game={game} />
    </React.Fragment>
  )
}
