const { speechSynthesis: speech } = window

export default class Speech {
  constructor(lang) {
    this.lang = lang
    this.voices = speech.getVoices()
  }

  get voice() {
    const { lang, voices } = this
    const name = lang.includes('zh') ? 'Google 國語（臺灣）' : 'Daniel'
    const index = voices.findIndex(voice => voice.name === name)
    return index ? voices[index] : voices[0]
  }

  speak = text => {
    // WORKAROUND: Not sure why React render the score speech game message twice in a short period of time.
    // Add `!speech.speaking` here to avoid speaking the same text for one speech game message.
    if (speech && !speech.speaking) {
      const utterance = new SpeechSynthesisUtterance(text)
      utterance.voice = this.voice
      speech.speak(utterance)
    }
  }
}
