import { useState } from 'react'
import { useHistory } from 'react-router-dom'
import { MenuList, MenuItem } from '@material-ui/core'
import { useTranslation } from 'react-i18next'
import config from 'config'
import { SoundEffectPlayer } from 'shared/services'
import { MenuItemSelector, Typography } from 'components'

export default function GameSelectionView(props) {
  const { appState } = props
  const { defaultGames: games } = config
  const history = useHistory()
  const { t } = useTranslation()
  const handleGameSelect = game => {
    SoundEffectPlayer.play('press')
    appState.game = game
    history.push('/game-menu/players')
  }
  const getPreviousPlayedGameIndex = () => {
    if (Object.keys(appState.game).length) {
      return games.findIndex(g => g.id === appState.game.id)
    } else {
      return 0
    }
  }
  const [selectedIndex, setSelectedIndex] = useState(getPreviousPlayedGameIndex())
  return (
    <MenuList>
      <MenuItemSelector selectedIndex={selectedIndex} setSelectedIndex={setSelectedIndex}>
        {
          games.map((game, i) => (
            <MenuItem key={i} selected={i === selectedIndex} onMouseOver={() => SoundEffectPlayer.play('hover') }
                      onClick={() => handleGameSelect({ id: game.id, name: game.name, rounds: game.rounds, score: game.score }) }>
              <Typography variant='h2'>{t(game.displayName)}</Typography>
            </MenuItem>
          ))
        }
      </MenuItemSelector>
    </MenuList>
  )
}
