import { useState } from 'react'
import { BackgroundImageContext } from 'components/contexts'
import { BackgroundImage } from 'shared/services'

const image = new BackgroundImage()

export default function BackgroundImageProvider(props) {
  const [backgroundImage, setBackgroundImage] = useState(image.randomImage)
  return (
    <BackgroundImageContext.Provider value={{
      backgroundImage,
      setBackgroundImage,
      setRandomBackgroundImage: () => setBackgroundImage(image.randomImage)
    }}>
      {props.children}
    </BackgroundImageContext.Provider>
  )
}
