import { useTranslation } from 'react-i18next'
import { List, ListItem, ListItemText } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import { Typography } from 'components'
import Score from './Score'
import styles from './styles'

const useStyles = makeStyles(styles)

export default function Scoreboard(props) {
  const { className, index, rounds, totalRounds } = props
  const { t } = useTranslation()
  const classes = useStyles()
  const lastEightRounds = rounds.slice(-8)
  return (
    <div className={className}>
      {
        <List>
          <ListItem className={classes.round}>
            <ListItemText primary={
              <Typography variant='h4'>
                {`${rounds.length} / ${totalRounds} ${t('Rounds')}`}
              </Typography>
            }/>
          </ListItem>
          {
            [...Array(lastEightRounds.length).keys()].map(i => (
              <ListItem key={i} className={classes.round}>
                <ListItemText primary={
                  lastEightRounds[i].scores.length === 0 ? <Typography variant='h5'>{`${lastEightRounds[i].index + 1}.`}</Typography> :
                    lastEightRounds[i].scores[0].displaySymbol === 'BUST' ?
                      <Typography variant='h5'>{`${lastEightRounds[i].index + 1}. ${t('Bust 😱')}`}</Typography> :
                      <Score
                        index={index}
                        round={lastEightRounds[i].index + 1}
                        score={lastEightRounds[i].scores.map(s => s.value).reduce((a, b) => a + b)}
                      />
                }/>
              </ListItem>
            )).reverse()
          }
        </List>
      }
    </div>
  )
}
