import { GameAwardUtils } from 'shared/services'
import { Score } from 'shared/services/Scoreboard'
import CountUpGame from '../../CountUpGameView/CountUpGame'

export default class NinjaCountUpGame extends CountUpGame {
  handleDartboardHit = scoreSymbol => {
    const { addScoreForCurrentPlayer, currentPlayerScoreboard: scoreboard } = this.state
    const symbol = Score.isInnerSingle(scoreSymbol) ? 'SBULL' : (scoreSymbol === 'SBULL' ? 'DBULL' : scoreSymbol)
    const score = addScoreForCurrentPlayer(new Score(symbol))
    if (score) {
      if (score.symbol === 'DBULL') {
        score.value = 100
      }
      this.state.addMessage(GameAwardUtils.getMessage(score))
      score.index === 2 && this.state.addMessage(GameAwardUtils.getMessage(scoreboard.getCurrentRound()))
      this.onDartboardHit(score)
    }
  }
}
