import { Howl } from 'howler'
import config from 'config'

const SOUND_EFFECTS = {
  default: [
    'hover', 'press', 'back', 'game_on', 'next',
    'dart', 'sbull', 'dbull', 'bust', 'win'
  ],
  ninja: ['bgm', 'dart', 'sbull', 'dbull']
}

export class SoundEffectService {
  theme = 'default'

  soundEffects = {}

  constructor() {
    this.setTheme(this.theme)
    config.soundTheme && config.soundTheme !== 'default' && this.setTheme(config.soundTheme)
  }

  play = soundEffect =>
    new Promise(resolve => {
      this.soundEffects[soundEffect].on('end', resolve)
      this.soundEffects[soundEffect].play()
    })

  stop = soundEffect => this.soundEffects[soundEffect].stop()

  setTheme = theme => {
    if (SOUND_EFFECTS[theme]) {
      this.theme = theme
    }
    this.loadSoundEffects()
  }

  loadSoundEffects = () => {
    SOUND_EFFECTS[this.theme].forEach(soundEffect => {
      this.soundEffects[soundEffect] = new Howl({
        src: [`/resources/sound-effects/${this.theme}/${soundEffect}.webm`],
        volume: soundEffect === 'bgm' ? 0.2 : 1.0,
        loop: soundEffect === 'bgm' ? true : false
      })
      this.soundEffects[soundEffect].on('loaderror', () => {
        this.soundEffects[soundEffect] = new Howl({
          src: [`/resources/sound-effects/default/${soundEffect}.webm`],
          volume: soundEffect === 'bgm' ? 0.2 : 1.0,
          loop: soundEffect === 'bgm' ? true : false
        })
      })
    })
  }
}

const soundEffectService = new SoundEffectService()

export default soundEffectService
